import React from 'react';
import './Header.css';
import Logo from '../images/logo.png';
import { Link } from 'react-router-dom';


class Header extends React.Component {  
  constructor(props) {
    super(props);
  }
  
  render() {
    return (
      <header className="header">
        <div className="header-container">
          <Link to={`/`}><img  class="logo" id="home" src={Logo} alt=""/></Link>
            <nav id="nav-content" className="header-navigation">
              <ul className="header-navigation__items">
                <li className="header-navigation__item"><Link to={`/`} style={{ textDecoration: 'none' }}>home</Link></li>
                <li className="header-navigation__item"><Link to={`/About`} style={{ textDecoration: 'none'}}>about</Link></li>
                <li className="header-navigation__item"><Link to={`/Works`} style={{ textDecoration: 'none'}}>works</Link></li>
              </ul>
            </nav>
          </div>
    
      </header>
    );
  }
}

export default Header;

