import React from 'react';
import { HashRouter, Route } from 'react-router-dom';
import Header from './component/Header';
import Home from './component/Home';
import About from './component/About';
import Works from './component/Works';
import TkHole from './component/TkHole';
import Portfolio from './component/Portfolio';
import Hibiki from './component/Hibiki';
import Memo from './component/Memo';
import Keity from './component/Keity';
import Retro from './component/Retro';
import Skate from './component/Skate';
import Juice from './component/Juice';
import Cosme from './component/Cosme';
import Drawing from './component/Drawing';
import ScrollToTop from './component/ScrollToTop.js';


class App extends React.Component {
  render() {
    return (
      <HashRouter>
        <ScrollToTop>
        <Header />
        <Route exact path={'/'} component={Home} />
        <Route path={'/About'} component={About} />
        <Route path={'/Works'} component={Works} />
        <Route path={'/TkHole'} component={TkHole} />
        <Route path={'/Portfolio'} component={Portfolio} />
        <Route path={'/Hibiki'} component={Hibiki} />
        <Route path={'/Memo'} component={Memo} />
        <Route path={'/Keity'} component={Keity} />
        <Route path={'/Retro'} component={Retro} />
        <Route path={'/Skate'} component={Skate} />
        <Route path={'/Juice'} component={Juice} />
        <Route path={'/Cosme'} component={Cosme} />
        <Route path={'/Drawing'} component={Drawing} />
        </ScrollToTop>
      </HashRouter>
    );
  };
}

export default App;