import React from 'react';
import './Footer.css';
import ScrollTop from '../images/scrollTop.png';
import FooterLogo from '../images/logo.png';
import { Link } from 'react-router-dom';

//上に戻るボタン関数
  const returnTop = () => {
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  };
  

class Footer extends React.Component {
  render() {                          
    return (
      <footer className="footer">
        <div className="footer-container">
          <Link to={`/`}><img  class="footer-logo" src={FooterLogo} alt=""/></Link>
          {/* <p className="footer-sentence">Shintaro's portfolio</p> */}
        <img className="scroll-top" src={ScrollTop} alt="" onClick={returnTop} />
        </div>
      </footer>
    );
  }
}

export default Footer;

